#playScreen {
  display: flex;
  width: 100%;
  background-color: #252525;
  justify-content: center;

  .action-button {
    position: absolute;
    left: 10px;
    top: 10px;
    width: 88px;
    height: 40px;
    font-size: 16px;
    white-space: nowrap;

    &:last-of-type {
      left: 104px;
    }
  }

  #game-map-container {
    display: flex;
    flex-direction: row;
    touch-action: manipulation;

    position: absolute;
    bottom: 40px;
    border: 1px solid black;

    .column {
      flex-direction: column;
    }
  
    .tile {
      position: relative;
      width: 100px;
      height: 100px;
      box-sizing: border-box;
      border: 2px solid black;

      &-overlap {
        height: 96px;
      }
    }
  }
}

.crazy {
  animation: gradient 15s ease infinite;
  background: linear-gradient(45deg, blue, purple, red, gold, green);
  background-size: 400% 400%;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

#info-container {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  top: 110px;

  border-radius: 8px;
  height: 100px;

  transition: all 0.5s ease-in;
  #info {
    transition: all 0.25s ease-in;
    display: flex;
    flex-direction: column;

    span {
      font-size: 26px;
      text-align: center;
      &:nth-child(2) {
        font-size: 22px;
      }
    }

    div {
      margin-top: 4px;
      display: flex;
      align-items: center;
      span {
        font-size: 24px;
        margin-right: 6px;
      }
      input {
        margin-top: 2px;
        font-size: 22px;
        width: 220px;
      }
      button {
        margin-left: 4px;
        font-size: 20px;
      }
    }

  }

}

#timer {
  display: flex;
  align-items: center;
  justify-content: center;
  transform-origin: top right;

  position: absolute;
  right: 10px;
  top: 10px;

  width: 60px;
  height: 40px;
  background-color: black;
  border-radius: 6px;
  text-align: center;
  transition: transform 0.5s ease-in;

  #player-face {
    position: absolute;
    top: -10px;
    right: 70px;
    width: 70px;
    height: 70px;
  }

  span { font-size: 20px }
}